const demo = [{
				avatar:"/static/default.png",
				username:"Sariel",
				update_time:1585036256,
				data:"内容内容内容内容内容内容内容内容内容内容",
				noread:"20"
			},{
				avatar:"/static/default.png",
				username:"Sariel",
				update_time:1585036256,
				data:"内容内容内容内容内容内容内容内容内容内容",
				noread:"20"
			},{
				avatar:"/static/default.png",
				username:"Sariel",
				update_time:1585036256,
				data:"内容内容内容内容内容内容内容内容内容内容",
				noread:"20"
			},{
				avatar:"/static/default.png",
				username:"Sariel",
				update_time:1585036256,
				data:"内容内容内容内容内容内容内容内容内容内容",
				noread:"20"
			}];
import msgList from '@/components/msg/msg-list.vue';
import noThing from '@/components/common/no-thing.vue';
import uniPopup from '@/components/uni-ui/uni-popup/uni-popup.vue';
export default {
	components:{
		msgList,
		noThing,
		uniPopup
	},
	data() {
		return {
			list: []
		}
	},
	// 页面加载
	onLoad() {
		this.list = demo
	},
	// 监听下拉刷新
	onPullDownRefresh() {
		this.refresh()
	},
	// 监听原生导航栏按钮事件
	onNavigationBarButtonTap(e) {
		switch (e.index){
			case 0: // 左边
			uni.navigateTo({
				url: '../user-list/user-list',
			});
			// 关闭弹出层
			this.$refs.popup.close()
				break;
			case 1: // 右边
			this.$refs.popup.open()
				break;
		}
	},
	methods: {
		// 下拉刷新
		refresh(){
			setTimeout(()=>{
				this.list = demo
				// 停止下拉刷新
				uni.stopPullDownRefresh()
			},2000)
		},
		// 弹出层选项点击事件
		popupEvent(e){
			switch (e){
				case 'friend':
				uni.navigateTo({
					url: '../search/search?type=user',
				});
					break;
				case 'clear':
				console.log('清除列表')
					break;
			}
			// 关闭弹出层
			this.$refs.popup.close()
		},
	}
}
